// ---
let document = null
let element = null
let mapper = null

const setDocument = (json) => {
  document = Document.fromJSON(json)
  element = DocumentView.render(document)
  mapper = new LocationMapper(element)
}

const findContainer = (path) => {
  let el = element
  for (const index of path) {
    el = el.childNodes[index]
  }
  return el
}

